.tic-tac-toe {
  width: 320px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: rgb(168, 85, 193);

  &__cell {
    width: 100px;
    height: 100px;
    border: 1px solid black;
    font-size: 90px;
    color: rgb(196, 196, 196);
    background-color: rgb(68, 66, 94);
  }
}
